header {
  background-color: #00205B;
  color: #fff;
  font-family: 'Helvetica', sans-serif;
  font-weight: bold;
}

.search-bar {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 10px;
}

.search-input {
  width: 300px;
  height: 20px;
  font-size: 20px;
  padding: 8px;
  border-radius: 0.5rem;
  margin-bottom: 1rem;
}

.search-button, .search-buttons {
  height: 40px;
  font-size: 20px;
  border-radius: 0.5rem;
}

.search-button{
  margin-bottom: 1rem;
  margin-top: 1.4rem;
}

.upload-button {
  height: 40px;
  font-size: 20px;
  float: right;
}

/* Styles for the sidebar */
.sidebar {
  width: 300px;
  background-color: #f5f5f5;
  padding: 20px;
  box-sizing: border-box;
  text-align: left;
  border-radius: 0.5rem;
}
.button, .sidebar{
  display: flex;
  flex-direction: column;
}

/* Styles for the expanded filter options */
.filter-options {
  display: flex;
  flex-direction: column;
  margin-top: 10px;
}

/* Styles for the collapsed Language filter */
.filter-label {
  cursor: pointer;
}

.filter-label::after {
  content: '+';
  float: right;
}

/* Styles for the expanded Language filter */
.filter-label.open::after {
  content: '-';
}

/* Additional styles for the app container */
.textplace {
  display: flex;
  flex-direction: column;
}

/* Styles for header, main, and footer */
header,
main,
footer {
  padding: 20px;
}

/* main */
main {
  display: flex;
  flex-direction: row;
}



.content div {
  background-color: #f5f5f5;
  height: 5rem;
  width: 60vw;

  margin-bottom: 2rem;
  margin-left: 2rem;

  box-shadow: 0 2px 4px rgba(126, 122, 122, 0.2);
  border-radius: 0.5rem;
}

/* Reset default margin and padding for the body */
body {
  margin: 0;
  padding: 0;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}